<template>
  <div class="page-container" style="width:99%;margin-top:15px;">
    <!-- <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>用户信息</span>
      </div>
      <div v-for="o in 4" :key="o" class="text item">{{'列表内容 ' + o }}</div>
    </el-card> -->
    <el-card class="box-card">
      <div class="match">
        <span @click="isMatch">开始匹配</span>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  components: {},
  methods: {
    isMatch:function(){
      this.$api.user
              .isMatch()
              .then(res => {
                if (res.success) {
                  this.$message({ message: "操作成功", type: "success" });
                } else {
                  this.$message({
                    message: "操作失败, " + res.msg,
                    type: "error"
                  });
                }
              })
              .catch(res => {
                this.editLoading = false;
                this.$message({
                  message: res.message,
                  type: "error"
                });
              });
    }
  }
};
</script>

<style>

.text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 45%;
    float: left;
    margin-right: 20px;
    text-align: center;
  }
  .match{
    height: 300px;
    width: 300px;
    border-radius: 300px;
    background-color: aquamarine;
    margin:auto;
    text-align: center;
    line-height: 300px;
    font-size: 40px;
    color: #fff;

  }
</style>


